@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600&display=swap');

@import "variables";
@import "values";
*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	// font-family: 'Raleway', sans-serif;
	font-family: 'Nunito', sans-serif;
	color: rgba(black, 0.8);
}

::selection{

	background: #6610F2;
	color: white;
}

h1, h2, h3, h4, h5, h6, p{
	font-weight: 400;
	i{
		position: relative;
		font-size: .8em;
		bottom: 2px;
		margin-right: 5px;
	}
}